.searcharea-container{
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightsteelblue;

}
.searcharea-input{
  width: 30vw;    
}
.searcharea-option{
box-sizing: border-box;
height: 40px;
margin-left: 10px;
color:white;
border: solid 1px black;
background-color:#008080;
border-radius: 7%;
padding-left:12px;
}


.collectionlist-container{
  box-sizing: border-box;
  padding : 20px 10vw;  
  background-color:  #4682B4; 
  display: flex;
  flex-wrap: wrap;
  height: 1000px;
  overflow-y: hidden;

  justify-content:center;
  width: 90vw;
  margin: 20px auto;
}
.collectionlist-container:hover{
  overflow-y: auto;
}
.collectionlist-container::-webkit-scrollbar{
width: 8px;
color: aliceblue;
border: solid 1px black;
margin: 0px;
}
::-webkit-scrollbar-thumb {
background: #b4ddf0; 
}